/**
 * EventBus    cc.EventTarget
 *  /    .
 */
export class ExtendedEventTarget extends cc.EventTarget {
  //     once,
  //      cc.EventTarget  .
  once(event: string, listener: (...args: unknown[]) => void): void {
    const callback = (...args: unknown[]) => {
      this.off(event, callback);
      listener(...args);
    };
    this.on(event, callback);
  }
}
